}*{transition:all .15s ease}.app-wrapper{display:grid;background-color:#f6f9fb;background-color:var(--color-page-background);grid-template-areas:"h h h" "n c c";grid-template-columns:2fr 10fr;grid-gap:10px;width:100%;margin:0 auto}.app-wrapper-content{grid-area:c;padding:10px 10px 10px 100px;background-color:#f6f9fb;color:#181818;width:60%}

.nav{grid-area:n;padding:10px;background-color:var(--color-article-background);display:flex;flex-direction:column;color:var(--color-text);margin-left:130px;border-radius:4px;height:-webkit-max-content;height:-moz-max-content;height:max-content}.nav a{text-decoration:none;padding:20px 30px;font-size:12px;line-height:11px;text-transform:uppercase;color:var(--color-text);border-top:1px solid #f4f4f4}.nav a:first-child{border-top:none}.nav a:hover,.nav a:focus,.nav a:active,.nav a.active{transition:0.2s;opacity:0.6;border-left:4px solid var(--color-border)}

.board-wrapper{display:flex;justify-content:center;flex-wrap:wrap;flex-direction:column}.board-table{display:flex;flex-wrap:wrap}.board-table .combination{width:100%;display:flex;flex-wrap:wrap;border:1px solid #E7E7E7;border-radius:8px;margin:20px 0px;padding:10px;flex-direction:column;height:350px}.board-table .combination .combination-result{height:20px;width:30px;border-radius:4px;padding:2px 5px;margin:0px 0px 0px 5px;display:flex;align-items:center;justify-content:center}.board-table .combination .combination-result .value{color:#161616;font-weight:700}.board-table .combination .combination-result .possible-value{color:#9e9e9e;font-weight:300}.board-table .combination>.combination-item{cursor:pointer;display:flex;align-items:center;flex:0 0 35px;padding:4px 15px;border-radius:4px;max-width:280px}.board-table .combination>.combination-item:hover{background:rgba(16,16,16,0.05)}.board-table .combination>.combination-item-name{font-size:16px;line-height:16px}.board-table .result{margin:20px 0px;padding:10px;border:1px solid #E7E7E7;border-radius:8px;width:100%;display:flex;flex-wrap:wrap;align-items:center;position:relative}.board-table .result .dice{padding:10px;background-color:var(--color-main);color:var(--color-text);border-radius:4px;margin:0px 5px;width:30px;height:30px;display:flex;justify-content:center;align-items:center;font-size:14px;line-height:16px}.board-table .result .dice.active{background-color:var(--color-main-active);color:var(--color-text-active);font-weight:700}.board-table .result-block{padding:10px 15px;position:absolute;left:110px;top:-40px;font-size:16px;line-height:16px;border-radius:27px;color:#fff;background:linear-gradient(215.49deg, #FFB75E 5.4%, #ED8F03 93.45%)}.messageAboutStep{font-size:14px;line-height:15px;padding:15px}.input-name{padding:5px;font-size:14px;line-height:16px;margin:15px 0px}

.plans-list{padding:10px 5px}.plans-list li{padding:4px 3px;font-size:14px;line-height:16px;cursor:pointer}.plans-list li:hover{opacity:0.5}.plans-list li.done{font-weight:700}

.winners{padding:20px 4px;display:flex;flex-wrap:wrap;flex-direction:column}.winners .winner-item{width:-webkit-max-content;width:-moz-max-content;width:max-content;display:flex;flex-wrap:wrap;padding:5px;margin:1px 2px 0px;border-top:1px solid #929292}.winners .winner-item:hover,.winners .winner-item:focus{background-color:rgba(96,96,96,0.1)}.winners .winner-item-name{min-width:150px}.winners .winner-item-score{min-width:45px}

@-webkit-keyframes scale{0%,100%{transform:scale(0)}7%,90%{transform:scale(0.4)}50%{transform:scale(1)}}@keyframes scale{0%,100%{transform:scale(0)}7%,90%{transform:scale(0.4)}50%{transform:scale(1)}}@-webkit-keyframes visible{0%{opacity:1}25%{opacity:0.8}50%{opacity:0.7}75%{opacity:0.8}100%{opacity:1}}@keyframes visible{0%{opacity:1}25%{opacity:0.8}50%{opacity:0.7}75%{opacity:0.8}100%{opacity:1}}@-webkit-keyframes spin-pre{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin-pre{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes spin-reverse-pre{0%{transform:rotate(0deg)}100%{transform:rotate(-360deg)}}@keyframes spin-reverse-pre{0%{transform:rotate(0deg)}100%{transform:rotate(-360deg)}}.preloader-container{opacity:1;position:relative}.preloader-container .preloader:before{content:"";position:fixed;top:calc(50% - 35px);left:calc(50% - 35px);width:70px;height:70px;border-radius:50%;border:3px solid transparent;border-top-color:#fff222;-webkit-animation:spin-reverse-pre 1.2s linear infinite;animation:spin-reverse-pre 1.2s linear infinite;z-index:110}.preloader-container .preloader:after{content:"";position:fixed;top:calc(50% - 50px);left:calc(50% - 50px);width:100px;height:100px;border-radius:50%;border:3px solid transparent;border-top-color:#fca719;-webkit-animation:spin-pre 0.8s linear infinite;animation:spin-pre 0.8s linear infinite;z-index:110}

